#controls {
  margin-bottom: 10px;
}

.enabled {
  cursor: pointer;
}

.disabled {
  cursor: auto;
  color: grey;
}

#controls i {
  padding-left: 5px;
  padding-right: 5px;
}

.badge > .strategies {
  display: none;
}

.badge:hover > .strategies {
  display: inline-block;
  border: none;
  /* padding: 0; */
}

.badge:hover > .strategyName {
  min-width: 13em;
}

.strategies {
  padding: 0;
}

.exerciseSwitch {
  font-size: 0.8em;
}